import React, {useState, useEffect} from 'react';
import axios from "axios";
import { Axios } from '../Axios';
import Header from './Header'
import CSRFToken from './CSRFToken';
import Part from './Part'
import Button from 'react-bootstrap/Button';
import DropdownButton from 'react-bootstrap/DropdownButton'
import Dropdown from 'react-bootstrap/Dropdown'
import Comment from './Comment';
import { withCookies, useCookies } from 'react-cookie';
import Cookies from 'js-cookie';


export default function Host_List() {

    const [bandList, setBandList] = useState([]);
    const [comment, setComment] = useState('')
    const [part, setPart] = useState({})
    const [reload, setReload] = useState('')


    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    useEffect(() => {
        const fetchData = async() => {
            const response = await Axios.get('views/band/', {
                params:{
                    guitar:part.guitar
                }
            });
            setBandList(response.data);
        };
        fetchData();
    },[]);

    const doChangePart = async(e) => {
        const response = await Axios.get('views/band/', {
            params:{
                [e.target.value]: e.target.checked
            }
        });
        setBandList(response.data);
    }



    const doGood = async(e) => {
        const res = await Axios.post('views/good/', {user: e.target.value}, config);
        window.location.reload(true);
    }

    const doComment = (e) => {
        setComment(e.target.value)
    }    

    const doSubmit = async(e) => {
        const response = await Axios.post('views/comment/', {
            user: e.target.value,
            comment: comment
        }, config);
        window.location.reload(true);
    }

    const doAccount = async(e) => {
        const response = await Axios.post('views/account/', {user: e.target.value}, config);
        console.log(response)
    }

    return (
        <div>
        <CSRFToken />
        <form>
        <div>
            <div className="form-check form-check-inline">
                募集パート
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part0" value={null} onChange={doChangePart} />
                <label className="form-chack-label" for="part0">なし</label>
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part1" value="guitar" onChange={doChangePart} />
                <label className="form-chack-label" for="part1">ギター</label>
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part2" value="base" onChange={doChangePart} />
                <label className="form-chack-label" for="part2">ベース</label>
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part3" value="drum" onChange={doChangePart} />
                <label className="form-chack-label" for="part3">ドラム</label>
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part4" value="vocal" onChange={doChangePart} />
                <label className="form-chack-label" for="part4">ボーカル</label>
            </div>
            <div className="form-check form-check-inline">
                <input className="form-check-input" type="radio" name="part" id="part5" value="keyboard" onChange={doChangePart} />
                <label className="form-chack-label" for="part5">キーボード</label>
            </div>
        </div>
        </form>
        <div className='container'>
        {bandList.map((user) => (
            <div className='card' key={user.id}>
                <div className='card-body'>
                    <h5 className='card-title'><img src={`${user.image}`} className="img-thumbnail" /></h5>
                    <h5 className='card-title'>名前：{user.name}</h5>
                    <h5 className='card-title'>年代：{user.age}</h5>
                    <h5 className='card-title'>ジャンル：{user.genre}</h5>
                    <h5 className='card-title'>募集パート：{user.guitar && 'ギター'} {user.base && 'ベース'} {user.drum && 'ドラム'} {user.vocal && 'ボーカル'} {user.keyboard && 'キーボード'} </h5>
                    <h5 className='card-title'>いいね：{user.good}</h5>
                    <button className="btn btn-primary" onClick={doGood} value={user.user}>いいね！</button>
                    <div className="input-group">
                    <input type="text" id={user.id} className="form-control" placeholder="comment" name='comment_data' onChange={doComment} />
                    <div>
                        <button className="btn btn-secondary" type="submit" onClick={doSubmit} value={user.user}>送信</button>
                    </div>
                    </div>
                    <Comment user={user.user} />
                </div>
                
            </div>
            ))}
        
        </div>
        </div>
    )
}

