import React, {useState, useEffect} from 'react';
import axios from "axios";
import ReactDOM from 'react-dom';
import Profile from './Profile';
import Upload_List from './Upload_List';
import Good_List from './Good_List';
import Cookies from 'js-cookie';
import {Axios} from '../Axios'
import Header from './Header'
import Login from './Login';
import Auth from './Auth';




export default function Mypage() {

    const [profile, setProfile] = useState(true);
    const [good, setGood] = useState(false);
    const [upload, setUpload] = useState(false);

    const doProfile = () => {
        setProfile(true)
        setGood(false)
        setUpload(false)
    }

    const doGood = () => {
        setProfile(false)
        setGood(true)
        setUpload(false)
    }

    const doUpload = () => {
        setProfile(false)
        setGood(false)
        setUpload(true)
    }


    console.log(profile)
    console.log(good)
    console.log(upload)

    function Component () {
        if (profile) {
            return <Profile />;
        } else if  (good) {
            return <Good_List />
        } else if (upload) {
            return <Upload_List />
        }
    }

    return(
        <div>
            <Auth />
                <input type="radio" class="btn-check" name="options" id="option1" autocomplete="off"  onClick={doProfile} checked={profile ? "true" : null} />
                <label class="btn btn-outline-success me-2" for="option1">Profile</label>
                <input type="radio" class="btn-check" name="options" id="option2" autocomplete="off" onClick={doGood} checked={good ? "true" : null}/>
                <label class="btn btn-outline-success me-2" for="option2">Good</label>
                <input type="radio" class="btn-check" name="options" id="option3" autocomplete="off" onClick={doUpload} checked={upload ? "true" : null}/>
                <label class="btn btn-outline-success me-2" for="option3">Upload</label>
            <Component />
        </div>

    )
}

