import React, {useState, useEffect, useRef} from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom';
import CSRFToken from './CSRFToken';
import Cookies from 'js-cookie';
import Auth from './Auth.js'
import { Axios } from '../Axios';

export default function Part(props) {
    const doChangePart = (e) => {
        props.setPart({ ...props.part, [e.target.value]: e.target.checked });
    }
    return(
        <div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name="part" id="part1" value="guitar" onChange={doChangePart} />
                <label className="form-chack-label" for="part1">ギター</label>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name="part" id="part2" value="base" onChange={doChangePart} />
                <label className="form-chack-label" for="part2">ベース</label>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name="part" id="part3" value="drum" onChange={doChangePart} />
                <label className="form-chack-label" for="part3">ドラム</label>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name="part" id="part4" value="vocal" onChange={doChangePart} />
                <label className="form-chack-label" for="part4">ボーカル</label>
            </div>
            <div className="form-check">
                <input className="form-check-input" type="checkbox" name="part" id="part5" value="keyboard" onChange={doChangePart} />
                <label className="form-chack-label" for="part5">キーボード</label>
            </div>
        </div>
)
}
